nav {
  display: flex;
  justify-content: space-around;
  padding: 10px 20px;
  background-color: #ffffff;
  border-radius: 14px;
  margin: 30px auto 15px;
  box-shadow: 0px 6px 0px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  width: 100%;
  z-index: 99;
  flex-shrink: 0;
}
@media (min-height: 800px) {
  nav {
    max-width: 800px;
  }
}
nav .nav-delete-answer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(180deg, #FE6161 0%, #BD3232 100%);
  margin-bottom: 10px;
  color: #ffffff;
  padding: 10px 20px;
}
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: portrait) and (-webkit-device-pixel-ratio: 2) {
  nav .nav-delete-answer {
    flex-shrink: 0;
    min-height: 55px;
  }
}
nav .nav-delete-answer svg {
  width: 25px;
  height: 25px;
}
@media (min-width: 800px) {
  nav .nav-delete-answer svg {
    width: 35px;
    height: 35px;
  }
}
nav .nav-delete-answer_count {
  background-color: #ffffff;
  padding: 3px;
  border-radius: 5px;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
nav .nav-delete-answer_count span {
  color: #E6442E;
  font-size: 17px;
}
@media (min-width: 800px) {
  nav .nav-delete-answer_count span {
    font-size: 29px;
  }
}
nav .nav-btn {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background: none;
  overflow: visible;
  padding: 10px 0;
}
nav .nav-btn p {
  color: rgba(0, 0, 0, 0.25);
  font-size: 13px;
  margin-top: 10px;
  transition: transform 0.6s ease;
  max-width: 90px;
}
@media (min-width: 800px) {
  nav .nav-btn p {
    max-width: 150px;
    font-size: 20px;
  }
}
nav .nav-btn_selected p {
  transform: scale(1.1);
}
nav .nav-btn_selected svg {
  transform: scale(1.1) rotate(360deg);
}
nav .nav-btn svg {
  transition: transform 0.4s ease;
  width: 40px;
  height: 40px;
}
@media (min-width: 800px) {
  nav .nav-btn svg {
    width: 55px;
    height: 55px;
  }
}

